@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400&display=swap);
body{
    font-family: 'Lato', 'sans-serif';
    font-weight: 300;
    padding: 0 0 0 0;
    margin: 0 0 0 0;
    min-height: 100%;
}

:root{
    --offwhite: #d7fdec;
    --grey: #938ba1;
    --mint: #a9fbd7;
    --bluegreen: #b2e4db;
    --greyblue: #b0c6ce;
    
}

.u-textCenter{
    text-align: center;
}

.u-bordering{
    border: 2px solid orangered;
}

.name-header{
    font-size: 60px;
    font-weight: 600;
}

.u-bluegreen{
    background-color: #b2e4db;
    background-color: var(--bluegreen);
}

.u-linked{
    color: black;
    text-decoration: none;
}
.u-linked:hover{
    cursor: pointer;
    text-decoration: underline;
    color: #0269a4;
}

.u-auto{
    margin-left: auto;
    margin-right: auto;
}

.big-text{
    font-size: 100px;
    font-weight: 300;
}

.smaller-text{
    font-size: 40px;
    font-weight: 400;
}

.return-home:hover{
    opacity: .75;
    cursor: pointer;
    transform: scale(1.2);
    transition: 0.7s ease-in-out;
}
.Icon-container{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: var(--mint);
    border-radius: 16px 16px 0px 0px;
    display: inline-flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    color: var(--grey);
    padding-top: 32px;
    padding-bottom: 32px;
    box-shadow: 0 0 30px -10px var(--mint);
    background-image: linear-gradient(to right, var(--bluegreen), var(--mint));
}

.logoicon{
    color: black;
    margin-left: 2%;
    margin-right: 2%;
}

.logoicon:hover{
    opacity: 0.75;
}
.About-container{
    display: inline-flex;
    flex-direction: row;
    flex-wrap: nowrap;
    /* align-items: center; */
    padding: 5%;
}

.About-pfp{
    border: 0px solid var(--grey);
    border-radius: 32px;
    box-shadow: 0 0 40px  var(--grey);
    max-width: 278px;
    height: 478px;
    margin-right: 5%;
}

.image-container{
    max-width: 278px;
    max-height: 478px;
    margin-right: 5%;

}

.bio-container{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

}

.bio-text{
    font-size: 28px;
}

.bio-routes{
    text-decoration: underline;
    cursor: pointer;
    color: black;
}

.bio-routes:hover{
    color: #0269a4;
}

.Courses-container{
    display: flex;
    flex-direction: row;
    margin-left: 2%;
    margin-right: 2%;
    justify-content: center;
    flex-wrap: wrap;
}

.show-button:hover{
    cursor: pointer;
    text-decoration: underline;
}

.courses-subcontainer{
    display: flex;
    flex-direction: column;
    padding-left: 5%;
    padding-right: 5%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;

}

.projects-container{
    display: flex;
    flex-direction: column;
    padding-left: 5%;
    padding-right: 5%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.site-card{
    background-color: #fefdf0;
    border-radius: 16px;
    margin-bottom: 8%;
    margin-top: 5%;
    padding-left: 1%;
    padding-right: 1%;
    padding-bottom: 2%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    box-shadow: 0 0 40px  #ccc;
}

.site-card:hover{
    transform: scale(1.2);
    transition: 0.8s ease-in-out;
    cursor: pointer;
}

.site-img{
    max-width: 500px;
    margin-right: auto;
    margin-left: auto;
    border-radius: 8px;
}

.single-class{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    background-color: #fefdf0;
    border-radius: 8px;
    margin-top: 5%;
    padding-left: 2%;
    padding-right: 2%;
    width: 100%;
    box-shadow: 0 0 40px  #ccc;
    padding-bottom: 2%;
}

.title-text{
    font-size: 30px;
    font-weight: 300;
}

.name-div{
    margin-left: auto;
    margin-right: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 2%;
}

.description-text{
    max-width: 500px;
}
.navbar-container {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-evenly;
    max-width: 100%;
    padding-left: 1%;
    padding-right: 1%;
    background-color: var(--bluegreen);
}

.homepage-container{
    display: flex;
    flex-direction: column;
    min-height: 100vh;

}

.content-container{
    flex-grow: 1;
}

.header-links:hover{
    opacity: 0.75;
    border-bottom: 3px solid var(--grey);
    cursor: pointer;
}

.selected-link{
    border-bottom: 3px solid var(--grey);
}
